import React, { useEffect } from 'react';
import './App.css';
import Particles from 'react-particles-js';
import { FaUserTie, FaHome, FaRegEye } from "react-icons/fa";
function App() {
  useEffect(() => {
    var words = document.getElementsByClassName('word');
    var wordArray = [];
    var currentWord = 0;

    words[currentWord].style.opacity = 1;
    for (var i = 0; i < words.length; i++) {
      splitLetters(words[i]);
    }

    function changeWord() {
      var cw = wordArray[currentWord];
      var nw = currentWord == words.length - 1 ? wordArray[0] : wordArray[currentWord + 1];
      for (var i = 0; i < cw.length; i++) {
        animateLetterOut(cw, i);
      }

      for (var i = 0; i < nw.length; i++) {
        nw[i].className = 'letter behind';
        nw[0].parentElement.style.opacity = 1;
        animateLetterIn(nw, i);
      }

      currentWord = (currentWord == wordArray.length - 1) ? 0 : currentWord + 1;
    }

    function animateLetterOut(cw, i) {
      setTimeout(function () {
        cw[i].className = 'letter out';
      }, i * 80);
    }

    function animateLetterIn(nw, i) {
      setTimeout(function () {
        nw[i].className = 'letter in';
      }, 340 + (i * 80));
    }

    function splitLetters(word) {
      var content = word.innerHTML;
      word.innerHTML = '';
      var letters = [];
      for (var i = 0; i < content.length; i++) {
        var letter = document.createElement('span');
        letter.className = 'letter';
        letter.innerHTML = content.charAt(i);
        word.appendChild(letter);
        letters.push(letter);
      }

      wordArray.push(letters);
    }

    changeWord();
    setInterval(changeWord, 4000);


  }, [])
  const hanldeActiveClass = () => {
    document.body.classList.remove('active')
  }
  const handleToggleClass = () => {
    document.body.classList.toggle('active')
  }
  return (
    <div className="App">
      <Particles
        params={{
          "particles": {
            "number": {
              "value": 160,
              "density": {
                "enable": false
              }
            },
            "size": {
              "value": 3,
              "random": true,
              "anim": {
                "speed": 4,
                "size_min": 0.3
              }
            },
            "line_linked": {
              "enable": false
            },
            "move": {
              "random": true,
              "speed": 1,
              "direction": "top",
              "out_mode": "out"
            }
          },
          "interactivity": {
            "events": {
              "onhover": {
                "enable": true,
                "mode": "bubble"
              },
              "onclick": {
                "enable": true,
                "mode": "repulse"
              }
            },
            "modes": {
              "bubble": {
                "distance": 250,
                "duration": 2,
                "size": 0,
                "opacity": 0
              },
              "repulse": {
                "distance": 400,
                "duration": 4
              }
            }
          }
        }} />
      <div className="pie pie1" onClick={hanldeActiveClass}>
        <div className="pie-color pie-color1">
          <FaRegEye className="eye" />
        </div>
      </div>
      <div className="pie pie2" onClick={hanldeActiveClass}>
        <div className="pie-color pie-color2">
          <FaUserTie className="user" />
        </div>
      </div>
      <div className="pie pie3" onClick={hanldeActiveClass}>
        <div className="pie-color pie-color3">
          <FaHome className="home" />
        </div>
      </div>
      <div className="menu" onClick={handleToggleClass}>
        <svg className="hamburger" xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 100 100">
          <g
            fill="none"
            stroke="#000"
            strokeWidth="7.999"
            strokeLinecap="round"
          >
            <path d="M 55,26.000284 L 24.056276,25.999716" />
            <path d="M 24.056276,49.999716 L 75.943724,50.000284" />
            <path d="M 45,73.999716 L 75.943724,74.000284" />
            <path d="M 75.943724,26.000284 L 45,25.999716" />
            <path d="M 24.056276,73.999716 L 55,74.000284" />
          </g>
        </svg>
      </div>
      <div className="name">
        <p className="mb-0">Shiori Yamaguchi</p>
      </div>
      <div className="text">
        <p>I am a </p>
        <p>
          <span className="word green">Mobile-App-developer.</span>
          <span className="word wisteria">Software-Engineer.</span>
          <span className="word belize">Web-designer.</span>
          <span className="word midnight">Front-End-developer.</span>
          <span className="word pomegranate">Back-End-developer.</span>
        </p>
      </div>
    </div>
  );
}

export default App;
